export const UPDATE_ACTIVE_VIEW = 'global/UPDATE_ACTIVE_VIEW'


const initialState = {
  activeView: "students"
}

export default (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_ACTIVE_VIEW:
      return {
        ...state,
        activeView: action.data
      }

    default:
      return state
  }
}

export const changeView = (activeView) => {
  return dispatch => {
    dispatch({
      type: UPDATE_ACTIVE_VIEW,
      data: activeView
    })
         
  }
}


