import React from 'react'
import './studentList.css'
import ListHeader from '../global/ListHeader'

import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {
  changeView,
} from '../../modules/globalStore'

const StudentListItem = ({sortable_name, avatar_url, enrollments}) => {

  return (
      <div className="StudentListItem">
        <div className="profile-pic" style={{backgroundImage: `url(${avatar_url})`}}>
        </div>
        <div className="list-item-details">
          <div className="list-item-title">
            {sortable_name}
          </div>
          <div className="list-item-detail-row">
            <span className="context">Current Score:</span> <span className="highlight">{getCurrentGrade(enrollments)}</span>
          </div>
        </div> 
      </div>
    )
}

const getCurrentGrade = (enrollments) => {
  let enrollment = enrollments[0] || {}
  let grades = enrollment.grades || {}
  return grades.current_score
}

class StudentList extends React.Component {
  constructor(props){
    super(props)
  }

  render() {
    let students = this.props.students || []
    console.log("this.props.activeView", this.props.activeView)
    return (
      <div className="ListColumnContainer">
        <ListHeader activeView={this.activeView} />
        <div className="StudentList">
          {students.map((studentListItem, key) => {
              return <StudentListItem 
                key={key}
                {...studentListItem}
              />
            }
          )}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return ({
    activeView: state.globalStore.activeView,
  })
}

const mapDispatchToProps = dispatch => {  
  return bindActionCreators({
    studentsView: () => changeView('students'),
    itemsView: () => changeView('items'),
  }, dispatch)
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(StudentList)



