
export const GRADEBOOK_ENV_REQUESTED = 'gradebook/GRADEBOOK_ENV_REQUESTED'
export const GRADEBOOK_ENV = 'gradebook/GRADEBOOK_ENV'

export const STUDENTS_REQUESTED = 'gradebook/STUDENTS_REQUESTED'
export const STUDENTS = 'gradebook/STUDENTS'



const initialState = {
  env: {},
  gradebook: {},
  fetchingEnv: false,
  fetchingStudents: false,
  students: []
}

export default (state = initialState, action) => {
  switch (action.type) {
    case GRADEBOOK_ENV_REQUESTED:
      return {
        ...state,
        fetchingEnv: true
      }

    case GRADEBOOK_ENV:
      const env = window.ENV || {}
      const gradebook = env.GRADEBOOK_OPTIONS || {}
      return {
        ...state,
        env: env,
        gradebook: gradebook,
        fetchingEnv: !state.fetchingEnv
      }

    case STUDENTS_REQUESTED:
      return {
        ...state,
        fetchingStudents: true
      }

    case STUDENTS:

      return {
        ...state,
        students: action.data,
        fetchingStudents: !state.fetchingStudents
      }

    default:
      return state
  }
}

export const getEnv = () => {
  return dispatch => {
    dispatch({
      type: GRADEBOOK_ENV_REQUESTED
    })

    dispatch({
      type: GRADEBOOK_ENV
    })
  }
}

