import React from 'react'
// import { push } from 'react-router-redux'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {
  getEnv,
  getStudents,
} from '../../modules/gradebook'

import StudentList from './StudentList'

class Students extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      env: props.env,
      gradebook: props.gradebook,
      listView: "students",
      showList: false,
      fetchedStudents: false
    }
  }

  componentWillMount(){
    this.props.getEnv()
  }


  componentDidUpdate(){
    let gradebook = this.props.gradebook || {}
    let students_url = gradebook.students_url
    if(students_url && !this.state.fetchedStudents){
      this.state.fetchedStudents = true
      this.props.getStudents(students_url)
    }
  }

  render() {
    let students = this.props.students || []
    return (
      <div className="Students list-detail-container">
        <section className="list-column">
          <StudentList students={students}/>
        </section>
        <section className="detail-column">
          Hello!
        </section>
      </div>
    )
  }
}

const mapStateToProps = state => {
  console.log("State", state)
  return ({
    env: state.gradebook.env,
    gradebook: state.gradebook.gradebook,
    students: state.gradebook.students,
    fetchingEnv: state.gradebook.fetchingEnv,
  })
}

const mapDispatchToProps = dispatch => {  
  return bindActionCreators({
    getEnv,
    getStudents,
  }, dispatch)
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Students)



