import React from 'react'
// import { Route, NavLink } from 'react-router-dom'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {
  changeView,
} from '../../modules/globalStore'

import './listHeader.css'

class ListHeader extends React.Component {
	constructor(props){
		super(props)
	}

	render() {
		return (
			<div className="ListTabHeader">
				<div className={'tab-link ' + (this.props.activeView === "students" ? 'active' : '')}
					 onClick={this.props.studentsView}>Students</div>
				<div className={'tab-link ' + (this.props.activeView === "items" ? 'active' : '')}
					 onClick={this.props.itemsView}>Items</div>
		    </div>
		)
	}
}

const mapStateToProps = state => {
  return ({
    activeView: state.globalStore.activeView,
  })
}

const mapDispatchToProps = dispatch => {  
  return bindActionCreators({
    studentsView: () => changeView('students'),
    itemsView: () => changeView('items'),
  }, dispatch)
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ListHeader)


