import store from './store'
import App from './containers/app'

import 'sanitize.css/sanitize.css'
import './index.css'


const target = document.querySelector('#strongmind_gradebook')
console.log("Target", target)
render(
  <Provider store={store}>
    <App />
  </Provider>,
  target
)

